@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:wght@700;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Barlow:wght@600&family=Fraunces:wght@700;900&display=swap);
/*
  font-family: 'Barlow', sans-serif; 600
  font-family: 'Fraunces', serif; 700, 900
  font-family: 'Poppins', sans-serif; 700
*/

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar{
  width: 5px;
  
}

::-webkit-scrollbar-thumb{
  background:#0c8dce;
}
/* 
// font-family: 'Barlow', sans-serif;  // weight: 600
// font-family: 'Fraunces', serif;     // weight:700,900
*/

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228, 45%, 44%);
}

nav {
  width: 100%;
  height: 60px;

  display: flex;
  align-items: center;
  justify-content: space-between;

  padding: 35px 35px 0 35px;

  background: transparent;
  color: white;

  position: absolute;
  z-index: 1;
}

.logo {
  width: 400px;
  font-size: 30px;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}

.links {
  display: flex;
  align-items: center;

  list-style: none;

  font-size: 18px;
  font-family: "Barlow", sans-serif;
  font-weight: 400;
}

.links li {
  margin: 0 0 0 40px;
}

.links li a {
  color: unset;
  text-decoration: none;
  transition: 0.6s ease;
}

.links li a:hover { 
    color: #f0f0f0;
    transform: scale(1.1);
}

#contactBtn {
  width: 120px;
  height: 50px;

  outline: none;
  border: none;
  border-radius: 50px;

  font-size: 18px;
  letter-spacing: 2px;
  font-family: "Fraunces", serif;
  font-weight: 700;

  color: #2f2f2f;

  cursor: pointer;

  transition: 0.6s ease;
}

#contactBtn:hover {
  background: #e8e8e8;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 0px 4px 1px;
}


header {
  width: 100%;
  height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;

  background: no-repeat center url(/SunnySide-Agency-LandingPage/static/media/image-header.49b52ffa.jpg);
  background-size: cover;

  position: relative;
}

.header-title {
  font-family: "Fraunces", serif;
  font-weight: 900;
  font-size: 4rem;
  letter-spacing: 12px;
  color: #ffffff;
  margin-top: 25vh;
}

.arrowDownHeader{
  margin-top: 8vh;
}

#firstSection {
  width: 100%;
  height: 80vh;

  display: flex;
}

#firstSectionContent {
  width: 50%;
  height: 80vh;
  background: #f6f6f6;
  display: flex;
  align-items: center;
  justify-content: center;
}

#firstSectionImg {
  width: 50%;
  height: 80vh;
}

.content {
  width: 500px;
  height: 400px;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.content h1,
p,
a {
  margin: 20px 0;
}

.content h2 {
  width: 450px;
  font-size: 40px;
  font-family: "Fraunces", serif;
  font-weight: 700;
  color: #2f2f2f;
}

.content p {
  font-size: 20px;
  font-family: "Barlow", sans-serif;
  font-weight: 400;
  color: #555;
}

.content a {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;

  font-size: 20px;
  font-family: "Fraunces", serif;
  font-weight: 700;

  color: #2f2f2f;
  text-decoration: none;

  border-bottom: 2px solid rgb(255, 217, 0);

  transition: 0.6s ease;
}

.content a:hover{
    transform: scale(1.05);
    color: #555;
}

#secondSection {
  width: 100%;
  height: 80vh;
  display: flex;
}

#secondSectionContent {
  width: 50%;
  height: 80vh;
  background: #f6f6f6;
  display: flex;
  align-items: center;
  justify-content: center;
}

#secondSectionImg {
  width: 50%;
  height: 80vh;
}

#secondSection .content a {
  border-bottom: 2px solid #fe7867;
}

#thirdSection {
  width: 100%;
  height: 80vh;
  display: flex;
}

#thirdSectionLeftContent,
#thirdSectionRightContent {
  width: 50%;
  height: 90vh;

  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  text-align: center;
}

#thirdSectionLeftImg,
#thirdSectionRightImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  z-index: -1;
}

#thirdSection .content {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-top: 40vh;
  z-index: 1;
}

#thirdSection .content h2 {
  opacity: 0.88;
}

#thirdSection .content p{
    width: 360px;
    text-align: center;
}

#fourthSection {
  width: 100%;
  height: 100vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  background-color: #f5f5f5;
}

#clientTestimonials {
  width: 80%;
  height: 80vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

#clientTestimonials h2 {
  font-size: 1.6rem;
  font-family: "Fraunces", serif;
  font-weight: 700;

  opacity: 0.88;

  letter-spacing: 8px;

  color: #a9a9a9;
}

#clientCards {
  width: 100%;
  height: 90%;

  display: flex;
  justify-content: space-evenly;
}

.card {
  width: 30%;
  height: 100%;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;

  text-align: center;
}

.cardImage {
  width: 100px;
  height: 100px;
  border-radius: 100%;
}

.cardImage img {
  width: 100%;
  height: 100%;
  border-radius: 100%;
  object-fit: cover;
}

.card p {
  font-family: "Barlow", sans-serif;
  font-weight: 600;
  font-size: 1.05rem;

  line-height: 30px;

  color: #333;

  opacity: 0.85;
}

.clientDescription {
  width: 100%;
  height: 10vh;
}

.clientDescription h4 {
  font-size: 1.2rem;
  font-family: "Fraunces", serif;
  font-weight: 700;

  color: #222;

  margin-bottom: -15px;
}

.clientDescription p {
  font-size: 1rem;
  font-weight: 300;
  color: #9d9d9d;
}

#fifthSection {
  width: 100%;
  height: 80vh;

  display: flex;
}

.fifthSectionImg {
  width: 25%;
  height: 100%;
  position: relative;
}

.fifthSectionImg img {
  width: 100%;
  height: 100%;
  position: absolute;
  object-fit: cover;
}

footer {
  width: 100%;
  height: 85vh;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  background: hsl(168, 48%, 60%);
  position: relative;
}

#footerContent {
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
}

#footerContent h1 {
  font-family: "Poppins", sans-serif;
  font-size: 2.2rem;
  font-weight: 700;
  color: hsl(168, 33%, 29%);
}

.footerLinks,
.footerSocialMediaLinks {
  width: 100%;
  height: 50px;

  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  margin: 2rem;

  list-style: none;
}

.footerLinks li {
  margin: 0 30px;
}

.footerLinks li a,
.footerSocialMediaLinks li a {
  font-size: 18px;
  font-family: "Barlow", sans-serif;
  font-weight: 600;
  color: hsl(168, 33%, 29%);
  text-decoration: none;
  transition: 0.6s ease;
}

.footerSocialMediaLinks {
  margin: 85 0px;
}

.footerSocialMediaLinks li {
  margin: 0 15px;
}

.footerSocialMediaLinks svg {
  width: 2rem;
  height: 1.5rem;
}

.footerLinks li a:hover {
  color: hsl(169, 36%, 39%);
}

.footerSocialMediaLinks li a:hover {
  color: hsl(169, 36%, 39%);
}

#by,
#by a {
  font-size: 0.9rem;
  font-family: "Barlow", sans-serif;
  font-weight: 600;
  color: hsl(168, 49%, 28%);
  text-decoration: none;
}

#by{
    position: absolute;
    bottom: 0;
}

#by a {
  border-bottom: 1px solid hsl(168, 49%, 28%);
}

